
let numberPad = require('Helper').numberPad;

cc.Class({
	extends: cc.Component,

	properties: {
		time_mb: cc.Label,
	},

	update (dt) {
		// ch n
		let countDownDate = new Date();
			countDownDate.setHours(18, 0, 0, 0, 0);
		countDownDate = countDownDate.getTime();
		// Ly thi gian hin ti
		let now = new Date().getTime();
	 
		// Ly s thi gian chnh lch
		let distance = countDownDate - now;
	 
		// HIn th chui thi gian trong th p
		if (distance < 0) {
			this.time_mb.string = '';
		}else{
			// Tnh ton s ngy, gi, pht, giy t thi gian chnh lch
			let hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
			let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
			let seconds = Math.floor((distance % (1000 * 60)) / 1000);
			this.time_mb.string = numberPad(hours, 2) + ':' + numberPad(minutes, 2) + ':' + numberPad(seconds, 2);
		}
	},
});
